<!-- 消息处理提示 -->
<template>
	<cover-view class="xh-dialog xh-msg-dialog" :style="{display: isShow?'block':'none'}">
		<cover-view class="xh-dialog-body xhDialog">
			<!-- toast 背景 -->
			<cover-image class="dialog-bg" src="../static/images/dialog_bg.png"></cover-image>
			<!-- toast header -->
            <cover-image class="dialog-bg-header-26" src="../../../static/images/dialog_tips_header.png"></cover-image>
			<!-- 主要内容 -->
			<cover-view class="xh-msg-dialog-content">
				<!-- 未中奖提示 -->
				<cover-view class="msg">{{msg}}</cover-view>
				<!-- 温馨提示 -->
				<cover-view class="msg-tips-header" v-if="msgSmall">温馨提示</cover-view>
				<!-- 副消息 -->
				<cover-view class="msg-small" v-if="msgSmall">
					{{msgSmall}}
				</cover-view>
			</cover-view>
            <!-- 按钮部分 -->
			<cover-view class="xh-dialog-btn">
				<!-- 马上兑换 -->
				<cover-view class="xdb-item" @click="callBack">
					<cover-view class="xdb-item-text suer-btn">{{buttonText}}</cover-view>
					<cover-image class="xdb-item-bg" src="../../../static/images/dialog_btn_bg01.png"></cover-image>
				</cover-view>
			</cover-view>

		</cover-view>
		<!-- 关闭按钮 -->
		<cover-image class="xh-dialog-close fadeInOpacity" :style="{display: isShow?'block':'none'}"
			src="../static/images/toast_close.png" @click="close" />
		<!-- 黑幕 -->
		<cover-view class="xh-dialog-black" @touchmove.stop></cover-view>
	</cover-view>
</template>

<script>
	//回调函数名
	let _method = ''
	export default {
		props: {
			buttonText: {
				type: String,
				default: '确认'
			}
		},
		data() {
			return {
				msg: '',
				msgSmall: '',
				isShow: false
			}
		},
		methods: {
			close() {
				//关闭弹窗
				this.isShow = false
				this.$emit('closeNotice')
			},
			show(res, method) { //{msg,msgSmall,method}
				//消息主标题
				this.msg = res.msg.replace('（异常）','')
				//副标题
				if (res.data && res.data.tips) {
					this.msgSmall = res.data.tips.replace('（异常）','')
				} else {
					this.msgSmall = ''
				}
				//回调函数名
				_method = method
				//特殊情况
				if (this.msg == '无效二维码') this.msgSmall = "请扫红牛“一元轻松享”拉环二维码"
				//显示弹窗
				this.isShow = true
			},
			callBack() {
				if (!_method) {
					this.close()
				} else if (_method) {
					this.$emit(_method)
					this.close()
				}
			}
		}
	}
</script>

<style lang="scss">
	.xh-msg-dialog {

		.xh-msg-dialog-content {
			position: absolute;
			top: 330rpx;
			left: 50%;
			transform: translateX(-50%);
			width: 450rpx;

			.msg {
				font-size: 38rpx;
				color: #FFFFFF;
				font-weight: 700;
				white-space: pre-wrap;
				text-align: center;
			}

			.msg-small {
				font-size: 24rpx;
				color: #FFFFFF;
				white-space: pre-wrap;
				text-align: center;
			}
			
			.msg-tips-header{
				color: #FFF33D;
				font-size: 24rpx;
				text-align: center;
				margin: 30rpx 0;
			}
		}

		.suer-btn {
			color: #614900;
		}
		
		.xh-dialog-close{
			bottom: 12%;
		}

	}
</style>
